import 'react-native-gesture-handler';
import { StatusBar } from 'expo-status-bar';
import React, { useState } from 'react';
import {
  Animated,
  Button,
  Platform,
  StyleSheet,
  Text,
  View,
  Dimensions,
  Image,
  Pressable
} from "react-native";
import { HomeScreen,ContactUs,Blog,ViewBlog } from './screens/';
import { ChatBot } from './components/';
import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { createDrawerNavigator } from '@react-navigation/drawer';
import * as Linking from 'expo-linking';

import AppLoading from "expo-app-loading";
import { Asset } from "expo-asset";
import Constants from "expo-constants";
import * as SplashScreen from "expo-splash-screen";
import * as Updates from "expo-updates";

SplashScreen.preventAutoHideAsync().catch(() => {
  /* reloading the app might trigger some race conditions, ignore them */
});

const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;
const isPhone = windowWidth < 600

const Stack = createNativeStackNavigator();
const Drawer = createDrawerNavigator();

//options={{ headerTitle: (props) => <Logo {...props} /> }}

const prefix = Linking.createURL('/');

export default function App() {



  const config = {
    screens: {
      Blog: {
        screens: {
          Recent: 'Blog/Recent/:',
          ViewBlog: 'Blog:dbKey',
        },
      },
      HomeScreen: 'Hello',
    },
  };

  const linking = {
    prefixes: [prefix],
  };

  const [isLoading,setLoading] = useState(true);
  const [isChatActive,setChatActive] = useState(false)

  if (isLoading && false){
    return(

        <Image source={require('./assets/loading.gif')}
          onLayout={()=>{
            setTimeout(() => {
              console.log("Stoped")
              setLoading(false);
            }, 4200);
          }}
          resizeMode='center'
          style={{
            width: "100%", height: "100%",
            backgroundColor:"rgba(254,252,254,1)"
            
          }}
         />

      
    )
  }else{
    return (
      <Pressable  
      onPress={()=>{setChatActive(false)}}
      style={{
        flex: 1,
        justifyContent: 'center',

      }}>

        <View style={{
          width: isChatActive?(isPhone ? windowWidth * 0.9 : windowWidth * 0.25):70,
          height: isChatActive ?(isPhone ? windowHeight * 0.9 : windowHeight * 0.5):70,

          backgroundColor:"#f2f2f2",
          borderRadius:20,

          position: 'absolute',
          right:20,
          bottom:20,
          zIndex:300,
          borderColor: "#ddd",
          borderWidth: 3,
             
          alignContent:"center",
          justifyContent:"center",
          overflow:"hidden"

        }}>
          <ChatBot
            isActive={isChatActive}
            onActiveChange={(is)=>{
              
              setChatActive(is)
            }}
          />
        </View>
      <NavigationContainer linking={linking}>
          {isPhone && <Drawer.Navigator initialRouteName="Home">
            <Drawer.Screen name="Home" component={HomeScreen} /> 
            <Drawer.Screen name="Contact Us" component={ContactUs} />
            <Drawer.Screen name="Blog" component={Blog} />
          </Drawer.Navigator>}
          {!isPhone && <Stack.Navigator>
            <Stack.Screen name="Home" component={HomeScreen} options={{headerShown:false}} />
            <Stack.Screen name="Contact Us" component={ContactUs} options={{ headerShown: false }} />
            <Stack.Screen name="Blog" component={Blog} options={{ headerShown: false }} />
            <Stack.Screen name="View Blog" component={ViewBlog} options={{ headerShown: false }} />
          </Stack.Navigator>}
        </NavigationContainer>
      </Pressable>

    );
  }
}







const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#0e2e61',
    alignItems: 'center',
    justifyContent: 'center',
  },
});
