import { Dimensions,StyleSheet, Text, View, ScrollView, ImageBackground, Button, TouchableOpacity } from 'react-native';
import React, { useState,useRef } from 'react';
import { FontAwesome } from '@expo/vector-icons';
import { Popable } from 'react-native-popable';

const windowWidth = Dimensions.get('window').width;
const windowHeight = Dimensions.get('window').height;
const isPhone = windowWidth < 600





export function Header(props){
    const [showPopover, setShowPopover] = useState(false)

    if (isPhone){
        return (
            <></>
        )
    }else{
        return (
            <View style={{ zindex:300,height: windowHeight * 0.13, justifyContent: "center", flexDirection: "row", alignItems: "center", borderBottomColor: "#bbb", borderBottomWidth: 2, width: "100%" }}>

                <TouchableOpacity onPress={() => { props.navigation.navigate("Home") }} ><Text style={[styles.headerButtonText, { borderWidth: props.isHome ? 2 : null }]}>Home</Text></TouchableOpacity>

                <TouchableOpacity onPress={() => { props.navigation.navigate("Blog") }} ><Text style={[styles.headerButtonText, { borderWidth: props.isBlog ? 2 : null }]}>Blog</Text></TouchableOpacity>
                <PopoverButton style={{zindex:300,}}/>
                <TouchableOpacity onPress={() => { props.navigation.navigate("Contact Us") }} ><Text style={[styles.headerButtonText, { borderWidth: props.isContact ? 2 : null }]}>Contact Us</Text></TouchableOpacity>

            </View>
        )
    }

}



function PopoverButton(){

    return(
        <TouchableOpacity
            onPress={() => { props.navigation.navigate("Contact Us") }}
        >
            <Text style={[styles.headerButtonText, { borderWidth: false ? 2 : null }]}>Services</Text>
            {/* <View style={{ zindex:300,position: 'absolute', top: 100, right:20,left:20,bottom:-300, backgroundColor: "red" }}>

            </View> */}
        </TouchableOpacity>
    )
}
